/* stylelint-enable */
:root {
  --transparent: transparent;
  --black: #000000;
  --dark: #1d1613;
  --gray: #9b9997;
  --white: #ffffff;
  --gold: #a87b62;
  --light: #f6f5f4;
  --poppins: "Poppins", "Arial", sans-serif;
  --cormorant-garamond: "Cormorant Garamond", "Times New Roman", serif; }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  font-family: var(--poppins);
  scroll-behavior: smooth; }

body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  margin: 0; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  text-decoration: none; }
  a[href]:hover, a[href]:focus {
    color: var(--gold); }
  a[href]:active {
    opacity: 0.6; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

h1,
h2,
h3,
p {
  margin: 0; }

.container {
  max-width: 1200px;
  min-width: 320px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  padding-top: 30px;
  padding-bottom: 19px;
  width: 100%; }

.header__wrap {
  border-bottom: 1px solid var(--light); }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  padding-bottom: 20px; }

.header form {
  padding-top: 2px;
  width: 400px; }

.header label {
  margin-right: 10px; }

.header input {
  width: 350px;
  height: 40px;
  border: none;
  background-color: var(--transparent); }
  .header input::placeholder {
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: var(--gray); }

.header__inner {
  display: flex;
  justify-content: flex-end;
  padding-top: 12px;
  width: 400px; }

.header__inner > * {
  margin-left: 40px;
  height: min-content;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: var(--dark); }

.header__inner button {
  padding: 0;
  border: none;
  background-color: var(--transparent);
  cursor: pointer; }

.navigation {
  display: flex;
  justify-content: center;
  padding-top: 17px;
  padding-left: 60px; }

.navigation__item {
  margin-right: 35px; }

.navigation__item a {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: var(--dark); }
